height: 25px;\n  width: 25px;\n  margin: 0 5px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-repeat: no-repeat;\n  background-position: center;\n}\n\n#header {\n  height: 60px;\n  width: 100vw;\n  /* background-color: rgb(10, 169, 10); */\n  background: linear-gradient(135deg, #3CD3AD, #4CB8C4);\n  position: fixed;\n  top: 0;\n  left: 0;\n  display: inline-flex;\n  align-items: center;\n  justify-content: space-between;\n  z-index: 20;\n  position: fixed;\n  \n}\n\n#header-logo {\n  height: 30px;\n  width: 200px;\n  margin-left: 50px;\n  color: white;\n  font-family: sans-serif;\n  font-size: 28px;\n  font-weight: 500;\n}\n\n\n\n\n\n/* CONTENT */\n\n#content-wrapper {\n  min-height: calc(100vh - 60px);\n  width: calc(100vw - 400px);\n  background-color: rgb(255, 255, 255);\n  margin-left: 400px;\n  padding: 60px;\n  display: flex;\n  align-items: center;\n  justify-content: flex-start;\n  flex-direction: column;\n  overflow: auto;\n}\n\n/* CONTENT HEADER */\n#content-header-container {\n  display: inline-flex;\n  align-items: flex-start;\n  justify-content: space-between;\n  flex-direction: row;\n  height: 100px;\n  background: linear-gradient(135deg, hsl(165, 51%, 20%), hsl(187, 43%, 29%));\n  position: fixed;\n  left: 400px;\n  top: 60px;\n  right: 0;\n  z-index: 10;\n}\n\n#content-header-label-container {\n  display: flex;\n  align-items: baseline;\n  flex-direction: row;\n  position: absolute;\n  left: 100px;\n  color: #e5f1ee;\n  top: 35px;\n}\n\n#content-header-date {\n  margin-left: 0.5em;\n  margin-top: 0.5em;\n  font-weight: 300;\n  color: #e5f1ee;\n  position: absolute;\n  top: 35px;\n  left: 46%;\n}\n\n#icons-container {\n  display: inline-flex;\n  align-items: center;\n  justify-content: space-evenly;\n  position: absolute;\n  right: 100px;\n  top: 38px;\n}\n\n#icon-person {\n  background-image: url(\"/src/Components/SVG/add-person-light.svg\");\n}\n#icon-person:hover {\n  background-image: url(\"/src/Components/SVG/add-person-light-fill.svg\");\n}\n\n#icon-settings {\n  background-image: url(\"/src/Components/SVG/3-dots-light.svg\");\n}\n\n#icon-settings:hover {\n  background-image: url(\"/src/Components/SVG/3-dots-light-fill.svg\");\n}\n\n#icon-chat {\n  background-image: url(\"/src/Components/SVG/chat-bubble-light.svg\");\n}\n\n#icon-chat:hover {\n  background-image: url(\"/src/Components/SVG/chat-bubble-light-fill.svg\");\n}\n\n\n\n\n\n\n\n\n\n/* MENU */\n\n.menu-element-container {\n  display: inline-flex;\n  align-items: center;\n  justify-content: flex-start;\n  flex-direction: row;\n  padding: 10px 0 10px 80px;\n  margin-bottom: 5px;\n  font-family: sans-serif;\n  width: 400px;\n  height: 40px;\n  border-radius: 5px;\n  cursor: pointer;\n}\n\n.menu-element-icon {\n  height: 18px;\n  width: 18px;\n}\n\n.menu-element-label {\n  color: #201602;\n  font-size: .95rem;\n  margin-left: 10px;\n  letter-spacing: .03em;\n}\n\n.menu-element-container > * {\n  pointer-events: none;\n}\n\n.menu-element-container:hover {\n  background-color: #cafaee;\n}\n\n.menu-element-container:active {\n  transition: background-color 0.05s ease-in-out;\n  background-color: rgb(184, 255, 228);\n}\n\n.count {\n  font-size: 12px;\n  margin: .5em;\n  font-weight: 500;\n  color: rgb(121, 121, 121);\n}\n\n#menu {\n  width: 400px;\n  height: calc(100vh - 60px);\n  background-color: hsl(180, 30%, 96%);\n  /* background:  hsla(37, 100%, 81%, 0.294); */\n  position: fixed;\n  bottom: 0;\n  border-right: 1px solid #dae7e4;\n  overflow: hidden;\n}\n\n#menu-items-container {\n  display: flex;\n  align-items: flex-start;\n  justify-content: flex-start;\n  flex-direction: column;\n  margin-top: 125px;\n  padding-left: 25px;\n}\n\n.menu-divide-line {\n  width: 150px;\n  margin: 20px 80px;\n  border-bottom: 1px solid rgb(202, 202, 202);\n}\n\n\n\n\n\n\n\n\n/* ADD TASK FORM */\n\n#add-task-form-container {\n  height: 500px;\n  min-width: 1100px;\n  display: none;\n  border: none;\n  background: linear-gradient(135deg, #3CD3AD, #4CB8C4);\n  position: fixed;\n  margin-bottom: -5em;\n  top: 15em;\n  right: 22em;\n  left: 30em;\n  z-index: 5;\n}\n\n#add-task-form-type-label {\n  color: white;\n  font-size: 1em;\n}\n\n.task-priority {\n  height: 50px;\n  width: 35px;\n}\n\n.hide {\n  max-height: 0 !important;\n}\n\n#dropdown-wrapper {\n  position: relative;\n  margin-bottom: 5em;\n}\n\n#add-task-dropdown-container-category, \n#add-task-dropdown-container-priority {\n  position: absolute;\n}\n\n#add-task-dropdown-container-priority {\n  top: 0;\n  left: 15em;\n}\n\n.icon {\n  pointer-events: none;\n}\n\n.dropdown {\n  width: 10em;\n  margin: 0 1em 1em 1em;\n  background-color: white;\n}\n\n.dropdown .title {\n  margin: .3em;\n  width: 45%;\n  pointer-events: none;\n}\n\n.container {\n  width: 100%;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  position: relative;\n  border: none;\n}\n\n.dropdown .title .fa-angle-right {\n  float: right;\n  margin-top: 0.2em;\n  transition: transform 0.3s;\n  right: .5em;\n}\n\n.fa-angle-right {\n  position: absolute;\n  right: .5em;\n}\n\n.dropdown .menu {\n  transition: max-height 0.5s ease-out;\n  max-height: 20em;\n  overflow: hidden;\n}\n\n.dropdown .menu .option {\n  margin: 0.3em 0.3em 0.3em 0.3em;\n  margin-top: 0.3em;\n}\n\n.dropdown .menu .option:hover {\n  background: rgba(83, 81, 81, 0.2);\n}\n\n.pointer-cursor:hover {\n  cursor: pointer;\n}\n\n.rotate-90 {\n  transform: rotate(90deg);\n}\n\n.link-background {\n  background-color: rgb(255, 255, 255);\n}\n\n.button {\n  background-color: white;\n  border: none;\n  font-family: sans-serif;\n  font-size: 1em;\n  padding: 0.6em 2em;\n  margin: 0 1em;\n  cursor: pointer;\n}\n\n.button:hover {\n  background-color: rgb(221, 221, 221);\n}\n\n#add-task-form-button-container {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-direction: row;\n  position: absolute;\n  bottom: 30px;\n  width: 100%;\n}\n\n#add-task-form {\n  min-width: 100%;\n  padding: 50px;\n  position: absolute;\n}\n\n#add-task-top-line-container {\n  display: inline-flex;\n  flex-direction: row;\n  justify-content: space-between;\n  width: 100%;\n  background-color: white;\n  margin-bottom: 2.5em;\n}\n\n#add-task-input {\n  display: flex;\n  border: none;\n  outline: none;\n  height: 36px;\n  width: 700px;\n  padding-left: 1em;\n  padding-top: 0.5em;\n  background-color: white;\n  font-size: 1em;\n  resize: none;\n}\n\n#task-input::-webkit-input-placeholder {\n  font-family: sans-serif;\n  font-size: 16px;\n}\n\n#date-input-container {\n  display: inline-flex;\n  align-items: center;\n  flex-direction: row;\n  width: 250px;\n}\n\n#date-input-container:hover {\n  background-color: rgba(240, 240, 240, 0.322);\n}\n\n#task-date {\n  width: 250px;\n  height: 36px;\n  font-family: sans-serif;\n  font-size: 0.9em;\n  display: flex;\n  align-items: center;\n  justify-content: flex-end;\n  background-color: rgba(255, 255, 255, 0);\n  padding-left: 5em;\n  padding-top: 0.3em;\n  margin-right: 2em;\n  color: #808080;\n  outline: none;\n  border: none;\n  cursor: pointer;\n  background-image: url(\"/src/Components/SVG/calendar-choose.svg\");\n  background-repeat: no-repeat;\n  background-position: right;\n}\n\n#add-task-description-container {\n  max-width: 100%;\n  min-height: 100px;\n  border: 1px solid rgb(207, 207, 207);\n  background-color: white;\n  margin-bottom: 2.5em;\n  padding: 0.3em 1em;\n  line-height: 2em;\n  display: flex;\n  align-items: flex-start;\n  border: none;\n}\n\n#add-task-description-input {\n  border: none;\n  outline: none;\n  font-size: 1em;\n  width: 100%;\n}\n\n#add-task-description-input::placeholder {\n  justify-content: flex-start;\n}\n\n.textarea {\n  font-size: 1em;\n  display: block;\n  font-family: sans-serif;\n  outline: none;\n  overflow: hidden;\n}\n\n.textarea[contenteditable]:empty::before {\n  content: \"Add task description\";\n  color: gray;\n}\n\n\n\n\n\n\n/* TASKS */\n\n#task-wrapper {\n  min-width: 100%;\n  min-height: 100vh;\n  margin-top: 100px;\n  padding: 10px;\n  display: flex;\n  flex-direction: column;\n  background-color: white;\n  overflow: scroll;\n} \n\n.task-date-divider-element {\n  width: 100%;\n  /* height: 1.5em; */\n  margin-top: 2em;\n  padding: 15px;\n  padding-top: 50px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-direction: column;\n  /* background: linear-gradient(135deg, rgba(195, 107, 253, 0.15), rgba(156, 237, 251, 0.13)); */\n  position: relative;\n  /* box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.356); */\n}\n\n.task-date-divider-label {\n  margin-bottom: 5px;\n  color: rgba(0, 0, 0, 0.63);\n  position: absolute;\n  top: 15px;\n  left: 20px;\n}\n\n.task-divider-content-container {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-direction: column;\n  width: 100%;\n}\n\n.task-element {\n  display: inline-flex;\n  align-items: center;\n  justify-content: space-between;\n  flex-direction: row;\n  padding-left: 10px;\n  width: 100%;\n  height: 50px;\n  overflow: hidden;\n  background-color: white;\n  border-bottom: 1px solid rgb(243, 243, 243);\n  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.096);\n}\n\n.task-element:hover {\n  background-color: hsl(0, 0%, 99%);\n}\n\n.delete,\n.edit {\n  display: flex;\n  height: 50px;\n  width: 50px;\n  opacity: 0;\n  background-repeat: no-repeat;\n  background-position: center;\n  cursor: pointer;\n  transition: opacity 0.7s ease-in-out;\n}\n.delete {\n  background-image: url(\"/src/Components/SVG/trash-can.svg\");\n  margin-right: 2em;\n}\n\n.edit {\n  background-image: url(\"/src/Components/SVG/edit-pencil.svg\");\n}\n\n.delete:hover,\n.edit:hover {\n  background-color: rgb(243, 243, 243);\n}\n\n.task-label-container {\n  display: flex;\n  align-items: center;\n  justify-content: flex-start;\n  max-height: 50px;\n  pointer-events: none;\n}\n\n.task-attributes-container {\n  display: flex;\n  align-items: center;\n  justify-content: flex-end;\n  max-height: 50px;\n}\n\n.task-category-container {\n  font-size: 14px;\n  font-weight: 300;\n  color: rgb(121, 121, 121);\n}\n\n.task-icon-container {\n  display: flex;\n  max-width: 50px;\n  max-height: 50px;\n}\n\n.team,\n.person,\n.avatar {\n  pointer-events: none;\n  display: none;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-image: url(\"/src/Components/SVG/team-icon.svg\");\n}"],"sourceRoot":""}]);
